.aboutme-description-p {
  text-align: left;
}

.aboutme-content-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin: 20px;
}

.content {
  display: flex;
  align-items: center;
  width: 100%;
}

.aboutme-description {
  margin: 25px;
  padding: 30px;
  max-width: 900px;
  width: 100%;
  height: auto;
  min-height: 300px;
  border-radius: 5px;
  background: rgb(47, 79, 79, 0.8);
  transition: background 1s, color 1s;
  font-size: 1.5em;
  color: white;
  cursor: pointer;
}

.aboutme-menubar {
  background: rgb(47, 79, 79, 0.8) !important;
}

.aboutme-menubar-items {
  color: white !important;
  cursor: pointer;
}

.techstack-image-container {
  display: flex;
  height: auto;
  justify-content: center;
  align-items: center;
}

.techstack-logos {
  height: 150px !important;
  width: 150px !important;
  border: none !important;
  background: rgb(47, 79, 79, 0.5);
  padding: 30px !important;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

.aboutme-description:hover {
  background: rgb(240, 219, 79, 0.7);
  color: black;
}

.ui.circular.image,
.ui.circular.image > *,
.ui.circular.images .image,
.ui.circular.images .image > * {
  min-width: 150px !important;
}

@media only screen and (max-width: 1120px) {
  .techstack-image-container {
    flex-direction: column;
  }
  .aboutme-description {
    max-width: 300px !important;
  }
  .aboutme-menubar-items {
    font-size: 0.8em;
  }
  .content {
    flex-direction: column;
  }
}
